import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { CardElementsAdjustmentBean } from "./bean/card-elements-adjustment.bean";
import { CALENDAR_CN } from "../../../../../@uisftech/common/constant/config";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";

@Component({
    selector: 'cardElementsAdjustment',
    templateUrl: './card-elements-adjustment.component.html',
    styleUrls: ['./card-elements-adjustment.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class CardElementsAdjustmentComponent implements OnInit {

    user: any;
    user1: any;
    userId: string;
    userName: string;
    orgId: string;
    orgName: string;
    handleDate: string;
    code: any;
    bean: CardElementsAdjustmentBean = new CardElementsAdjustmentBean();
    msgs: Message[];
    ch: any = CALENDAR_CN;
    nameList: any = [];
    noPromptDispaly: boolean = false;

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
    ) {
        //调用码值
        this.codeValues();
        this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user1.userId) {
            this.userId = this.user1.userId;
        }
        if (this.user1.orgId) {
            this.orgId = this.user1.orgId;
        }
        if (this.user.userName) {
            this.userName = this.user.userName;
        }
        if (this.user.orgName) {
            this.orgName = this.user.orgName;
        }

    }


    // 获取码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {

    }

    doSubmit() {
        // this.bean.operationType = '2'
        this.bean.agent = this.userId;
        this.bean.handleOrg = this.orgId;
        this.bean.handleDate = this.handleDate;
        this.httpService.updateCeCardElementAdjustApply(this.bean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                //   this.querySecle();
                // this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }
}