import { Routes, RouterModule } from "@angular/router";
import { ModuleWithProviders } from "@angular/core";
import { CardElementsAdjustmentComponent } from "./card-elements-adjustment/card-elements-adjustment.component";

export const routes: Routes = [
    {
        path: '',
        children: [
            {path: 'card-elements-adjustment',component: CardElementsAdjustmentComponent},
        ]
    }

]

export const CardElementsRouting: ModuleWithProviders = RouterModule.forChild(routes);